@font-face {
  font-family: 'bs';
  src: url("../fonts/blogger_sans-medium.woff2") format("woff2"), url("../fonts/blogger_sans-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'tangak';
  src: url("../fonts/tangak.woff2") format("woff2"), url("../fonts/tangak.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.container {
  max-width: 1140px;
  padding: 0 10px;
  margin: auto;
}

.large-container {
  max-width: 1250px;
  padding: 0;
  margin: auto;
}

.btn {
  height: 40px;
  background-color: #f6b52d;
  border: 1px solid white;
  border-radius: 16px;
  padding: 0 20px;
  font: 700 14px/40px "Open Sans", sans-serif;
  color: #fff;
  text-decoration: none;
  letter-spacing: 0.05em;
  transition: all .3s ease-in-out;
  display: block;
}

.btn:hover {
  background-color: #f37416;
}

.slick-slide,
.slick-slide * {
  outline: none !important;
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

