body {
  background: url(../img/bg.jpg) top center no-repeat;
  background-size: cover;
}

.site-header__top {
  background: #fff;
  border-bottom: 1px solid #86d564;
  height: 56px;
}

.site-header__menu {
  margin: 0;
  padding: 6px 0 0;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.site-header__menu a {
  padding: 0 18px;
  display: block;
  font: 400 16px/38px "Open Sans", sans-serif;
  color: #ff9c00;
  text-decoration: none;
  border: 2px solid transparent;
  border-radius: 18px;
  transition: all .3s ease-in-out;
}

.site-header__menu a:hover {
  background-color: #ff9c00;
  border-color: #f37416;
  color: #fff;
  text-shadow: 1px 1px 1px #fff;
}

.site-header__menu .current-menu-item a {
  background-color: #ff9c00;
  border-color: #f37416;
  color: #fff;
  text-shadow: 1px 1px 1px #fff;
}

.site-header__menu .login a {
  font-size: 19px;
  font-weight: 700;
  padding-left: 40px;
  background-image: url(../img/login-ico.png);
  background-repeat: no-repeat;
  background-position: left 7px center;
}

.site-header__menu .login a:hover {
  background-image: url(../img/login-ico.png);
  background-color: #f37416;
}

.site-header__row {
  padding: 20px 0 30px;
}

.site-header__logo {
  float: left;
  margin-top: 12px;
}

.site-header__logo a {
  display: block;
}

.site-header__logo a img {
  display: block;
  max-width: 100%;
  height: auto;
}

.site-header__cart {
  display: block;
  float: right;
  padding-left: 66px;
  text-decoration: none;
  background: url(../img/cart-ico.png) left bottom 5px no-repeat;
}

.site-header__cart span {
  display: block;
}

.site-header__cart .title {
  font: 800 17px "Open Sans", sans-serif;
  color: #f37416;
  margin-bottom: 5px;
  text-transform: uppercase;
}

.site-header__cart .description {
  font: 600 18px "Open Sans", sans-serif;
  color: #63a605;
}

.site-header__search {
  margin: 6px 260px 0 350px;
}

.site-header__search--top {
  overflow: hidden;
  margin-bottom: 15px;
}

.site-header__search .work-hours {
  font: 400 16px "Open Sans", sans-serif;
  color: #ff9c00;
  float: left;
  padding-left: 25px;
  background: url(../img/clock-ico.png) left center no-repeat;
}

.site-header__search .tel {
  text-decoration: none;
  font: 800 16px "Open Sans", sans-serif;
  color: #ff9c00;
  display: block;
  float: right;
  padding-left: 25px;
  background: url(../img/tel-ico.png) left center no-repeat;
}

.site-header__search--form {
  position: relative;
  padding-right: 125px;
}

.site-header__search .search__input {
  display: block;
  width: 100%;
  height: 32px;
  background-color: #fff;
  border: 1px solid #fdd635;
  border-radius: 16px;
  outline: 0;
  padding: 0 10px 0 40px;
  background-repeat: no-repeat;
  background-image: url(../img/search-ico.png);
  background-position: left 12px center;
}

.site-header__search .btn--submit {
  position: absolute;
  cursor: pointer;
  right: 0;
  top: 0;
  font-weight: 800;
  letter-spacing: 0.1em;
  padding-right: 32px;
  background-image: url(../img/submit-arr.png);
  background-position: right 17px top 9px;
  background-repeat: no-repeat;
  height: 32px;
  line-height: 32px;
}

.site-header__nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.site-header__nav a {
  border: 2px solid transparent;
  padding: 7px 12px;
  display: block;
  text-decoration: none;
  text-align: center;
  border-radius: 12px;
  height: 100%;
}

.site-header__nav a:hover {
  border-color: #fff;
  background-color: #f7d66b;
}

.site-header__nav a:hover .hover-ico {
  display: block;
}

.site-header__nav a:hover .main-ico {
  display: none;
}

.site-header__nav a:hover .title {
  color: #fff;
  text-shadow: 0 1px 1px rgba(202, 129, 3, 0.72);
}

.site-header__nav .title {
  font: 400 14px/17px "Open Sans", sans-serif;
  color: #f37416;
}

.site-header__nav .ico-wrap {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.site-header__nav .hover-ico {
  display: none;
}

.site-header__pages {
  display: flex;
  justify-content: space-around;
  margin: 20px 0 0;
  padding: 0;
  list-style-type: none;
}

.site-header__pages .btn {
  text-transform: uppercase;
  padding: 0 40px;
}

.simple-title {
  text-align: center;
  position: relative;
  margin: 30px 0;
  padding-bottom: 10px;
  font: 400 51px "tangak";
  color: #ff9c00;
}

.simple-title::after {
  content: '';
  height: 3px;
  background: #c7d4dc;
  width: 160px;
  position: absolute;
  left: 50%;
  margin-left: -80px;
  bottom: 0;
}

.products__slider {
  height: 375px;
  overflow: hidden;
  padding: 0 60px;
}

.products__slider .slick-slide {
  background: #fff;
  padding: 25px 20px 15px;
  border: 2px solid transparent;
  box-shadow: 0 -1px 10px rgba(61, 145, 81, 0.22);
  border-radius: 3px;
  margin: 10px;
  transition: all .3s ease-in-out;
  max-height: 375px;
}

.products__slider .slick-slide:hover {
  border-color: #ffce58;
  box-shadow: 0 -1px 21px rgba(255, 211, 106, 0.42);
}

.products__slider .slick-slide:hover img {
  transform: scale(1.15);
}

.products__slider .slick-slide:hover .title {
  color: #f37416;
}

.products__slider .slick-slide:hover .product-cart {
  opacity: 1;
}

.products__slider .slick-slide:hover .view-more__row {
  display: block;
}

.products__slider .img-wrapper {
  height: 160px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.products__slider .img-wrapper img {
  display: block;
  max-width: 100%;
  height: auto;
  transition: all 0.3s ease-in-out;
}

.products__slider .title {
  font: 700 14px/20px "Open Sans", sans-serif;
  color: #ff9c00;
  text-decoration: none;
  margin: 30px 0 10px;
  display: block;
  transition: all .3s ease-in-out;
}

.products__slider .price__col {
  float: left;
  margin-top: 2px;
}

.products__slider .price__col .old-price {
  font: 400 14px "Open Sans", sans-serif;
  color: #a1a1a1;
  text-decoration: line-through;
  margin-right: 10px;
}

.products__slider .price__col .actual-price {
  font: 800 22px "Open Sans", sans-serif;
  color: #8ec73f;
}

.products__slider .product-cart {
  display: block;
  float: right;
  background: url(../img/product-cart-ico.png) no-repeat;
  width: 38px;
  height: 38px;
  opacity: .8;
  transition: all .3s ease-in-out;
}

.products__slider .slick-list {
  margin: 0 -10px;
}

.products__slider .view-more__row {
  padding-top: 15px;
  display: none;
  overflow: hidden;
  transition: all .3s ease-in-out;
}

.products__slider .view-more {
  float: left;
  font: 700 14px "Open Sans", sans-serif;
  color: #a1a1a1;
  text-decoration: underline;
  text-transform: uppercase;
}

.products__slider .slick-arrow {
  position: absolute;
  font-size: 0px;
  text-indent: -9999px;
  padding: 0;
  top: 50%;
  margin-top: -16px;
  background: url(../img/sl-next.png);
  width: 33px;
  height: 33px;
  border: none;
  cursor: pointer;
  outline: 0;
}

.products__slider .slick-arrow.slick-prev {
  left: 0;
  transform: rotate(180deg);
}

.products__slider .slick-arrow.slick-next {
  right: 0;
}

.why-we {
  background: url(../img/why-bg.png) top center no-repeat;
  background-size: cover;
  margin-top: 60px;
  padding-top: 15px;
  padding-bottom: 50px;
}

.why-we__description {
  max-width: 600px;
  margin: auto;
  text-align: center;
  font: 400 14px/27px "Open Sans", sans-serif;
  color: #464646;
}

.why-we__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 30px -10px 0;
}

.why-we__item {
  max-width: 270px;
  text-align: center;
  margin: 0 10px;
}

.why-we .img-wrapper {
  width: 169px;
  height: 169px;
  background-color: rgba(255, 253, 225, 0.1);
  box-shadow: 0 3px 5px rgba(83, 83, 83, 0.15);
  border-radius: 50%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: auto;
  margin-bottom: 30px;
}

.why-we .img-wrapper img {
  display: block;
  max-width: 100%;
  height: auto;
}

.why-we .title {
  margin: 0 0 10px;
  padding-bottom: 10px;
  position: relative;
  font: 800 21px "Open Sans", sans-serif;
  color: #f37416;
}

.why-we .title::after {
  content: '';
  height: 3px;
  background: #fff;
  width: 110px;
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -55px;
}

.why-we .description {
  margin: 0;
  font: 400 14px/23px "Open Sans", sans-serif;
  color: #464646;
}

.footer-cat {
  background: #fff;
  padding-top: 20px;
  padding-bottom: 50px;
}

.footer-cat__row {
  display: flex;
  justify-content: space-between;
}

.footer-cat__col {
  padding-right: 25px;
  width: 25%;
}

.footer-cat .title {
  font: 800 21px "Open Sans", sans-serif;
  color: #a7d359;
  padding-bottom: 15px;
  margin: 0 0 15px;
  position: relative;
}

.footer-cat .title::after {
  content: '';
  background: #c7d4dc;
  height: 3px;
  width: 80px;
  position: absolute;
  left: 0;
  bottom: 0;
}

.footer-cat ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.footer-cat ul li {
  margin-bottom: 10px;
